<template>
	<div class="sidebar">
		<el-menu class="sidebar-el-menu" :default-active="defaultActive"
                   router
                   @open="handleOpen"
                   :collapse="isCollapse" >

				<template>
					<div class="app-side-fold"
						@click.prevent="toggleSideBar">
						<i v-show="!isCollapse" class="el-icon-s-fold"></i>
						<i v-show="isCollapse" class="el-icon-s-unfold"></i>
						<span v-show="!isCollapse" slot="title" class="sidebar-one">导航列表</span>
					</div>
				</template>
					<template v-if="routes.children && routes.children.length">
					<el-menu-item v-for="item in routes.children"
												:key="item.name"
												:index="item.name"
												@click="$router.push({name:item.name})"
						>
						<i class="el-icon-menu"></i>
						<span slot="title">{{ item.meta.title }}</span>
					</el-menu-item>
				</template>
    </el-menu>
	</div>
</template>

<script>

	export default {
		name: 'Sidebar',
		data() {
			return {
				defaultActive:'',
				 isCollapse: false,
			}
		},
		computed: {
			// 获取route路由
			routes () {
				return this.$router.options.routes[3]
			}
		},
		created() {
			console.log(this.routes)
		},
		methods: {
			toggleSideBar () {
			this.isCollapse = !this.isCollapse
			},
			handleOpen (key, keyPath) {
			console.log(key, keyPath);
			},
			handleClose (key, keyPath) {
			console.log(key, keyPath);
			},
			handleSelect (key, keyPath) {
			console.log(key, keyPath);
			},


		}
	}
</script>

<style lang="scss" scoped >
		.sidebar{
			display: block;
			position: absolute;
			left: 0;
			top: 70px;
			bottom: 0;
			overflow-y: scroll;
			background-color: transparent;
			background-image: url('../../../assets/img/sidebar_bg.png');
			background-size: cover;
			color: #fff;
			font-size: 18px;
			.sidebar-el-menu:not(.el-menu--collapse) {
				width: 220px;
			}
			.app-side-fold{
				position: absolute;
				top:10px;
				left:25px;
			}
			.el-menu{
				background-color: transparent;

			}
			.el-menu-item{
				font-size: 18px;
				color: #fff;
				position: absolute;
    			top: 30px;
				width:220px;
			}
			.el-menu-item:hover{
				//  opacity:0.6;
				//  background: yellow;
			}
		}
</style>



